<template>
    <div>
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>

        <el-card>
            <!-- & -->
            <el-row :gutter="20">
                <el-col :span="8">
                    <el-input placeholder="" v-model="queryInfo.query" clearable @clear="findAllData">
                        <el-button slot="append" icon="el-icon-search" @click="getOrderListData"></el-button>

                    </el-input>
                </el-col>
            </el-row>


            <!--  -->
            <el-table :data="orderListData" style="width: 100%" border stripe>
                <el-table-column type="index"></el-table-column>
                <el-table-column prop="order_number" label="">
                </el-table-column>
                <el-table-column prop="order_price" label="" width="75px">
                </el-table-column>
                <el-table-column label="" width="80px">
                    <template slot-scope="scope">
                        <el-tag type="success" v-if="scope.row.order_pay === 1"></el-tag>
                        <el-tag type="danger" v-else></el-tag>
                    </template>
                </el-table-column>
                <el-table-column prop="is_send" label="" width="70px">
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        {{ scope.row.create_time | dateFormat }}
                    </template>
                </el-table-column>
                <el-table-column label="" width="120px">
                    <template slot-scope="scope">
                        <!--  -->
                        <el-button type="primary" icon="el-icon-edit" size="mini" @click="setAddress"></el-button>
                        <!--  -->
                        <el-button type="success" icon="el-icon-location-outline" size="mini"
                            @click="lookLogistics"></el-button>
                    </template>
                </el-table-column>
            </el-table>

            <!--  -->
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                :current-page="queryInfo.pagenum" :page-sizes="[5, 10, 15, 20]" :page-size="queryInfo.pagesize"
                layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </el-card>

        <!--  -->
        <el-dialog title="" :visible.sync="setAddressDialog" width="50%">
            <!--  -->
            <el-form :model="setAddressForm" :rules="setAddressRules" ref="setAddressRef" label-width="100px">
                <el-form-item label="/" prop="area">
                    <!--  -->
                    <el-cascader :options="cityOptions" :value="city" :props="{ expandTrigger: 'hover' }"></el-cascader>
                </el-form-item>
                <el-form-item label="" prop="address">
                    <el-input v-model="setAddressForm.address"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="setAddressDialog = false"> </el-button>
                <el-button type="primary" @click="setAddressDialog = false"> </el-button>
            </span>
        </el-dialog>


        <!--  -->
        <el-dialog title="" :visible.sync="logisticsDialog" width="50%">
            <!--  -->
            <el-timeline>
                <el-timeline-item v-for="(item, index) in activities" :key="index" :timestamp="item.time">
                    {{ item.context }}
                </el-timeline-item>
            </el-timeline>
        </el-dialog>
    </div>
</template>

<script>
import cityOptions from '@/assets/js/city_data2017_element.js'
export default {
    props: {

    },
    data() {
        return {
            // 
            queryInfo: {
                query: '',
                // 
                pagenum: 1,
                // 
                pagesize: 10,
            },
            // 
            total: 0,
            // 
            orderListData: [],
            // 
            setAddressDialog: false,
            // 
            setAddressForm: {
                area: '',
                address: ''
            },
            // 
            setAddressRules: {
                area: [
                    { required: true, message: '/', trigger: 'blur' },
                ],
                address: [
                    { required: true, message: '', trigger: 'blur' }
                ]
            },
            // 
            cityOptions: cityOptions,
            // id
            city: [],
            // 
            logisticsDialog: false,
            // 
            activities: [
                {
                    "time": "2018-05-10 09:39:00",
                    "ftime": "2018-05-10 09:39:00",
                    "context": ",,",
                    "location": ""
                },
                {
                    "time": "2018-05-10 08:23:00",
                    "ftime": "2018-05-10 08:23:00",
                    "context": "[]  95338",
                    "location": ""
                },
                {
                    "time": "2018-05-10 07:32:00",
                    "ftime": "2018-05-10 07:32:00",
                    "context": " []",
                    "location": ""
                },
                {
                    "time": "2018-05-10 02:03:00",
                    "ftime": "2018-05-10 02:03:00",
                    "context": "[], []",
                    "location": ""
                },
                {
                    "time": "2018-05-09 23:05:00",
                    "ftime": "2018-05-09 23:05:00",
                    "context": " []",
                    "location": ""
                },
                {
                    "time": "2018-05-09 21:21:00",
                    "ftime": "2018-05-09 21:21:00",
                    "context": "[], []",
                    "location": ""
                },
                {
                    "time": "2018-05-09 13:07:00",
                    "ftime": "2018-05-09 13:07:00",
                    "context": " ",
                    "location": ""
                },
                {
                    "time": "2018-05-09 12:25:03",
                    "ftime": "2018-05-09 12:25:03",
                    "context": "",
                    "location": ""
                },
                {
                    "time": "2018-05-09 12:22:24",
                    "ftime": "2018-05-09 12:22:24",
                    "context": "HLA",
                    "location": ""
                },
                {
                    "time": "2018-05-08 21:36:04",
                    "ftime": "2018-05-08 21:36:04",
                    "context": "",
                    "location": ""
                }
            ]
        }
    },
    methods: {
        // input
        findAllData() {
            this.getOrderListData()
        },
        // 
        async getOrderListData() {
            const { data: result } = await this.$axios.get('orders', { params: this.queryInfo })
            if (result.meta.status != 200) {
                return this.$message.error('!')
            }

            // console.log(result);
            this.orderListData = result.data.goods
            this.total = result.data.total
        },
        // 
        handleSizeChange(newSize) {
            this.queryInfo.pagesize = newSize
            this.getOrderListData()
        },
        // 
        handleCurrentChange(newPage) {
            this.queryInfo.pagenum = newPage
            this.getOrderListData()
        },
        // 
        setAddress() {
            this.setAddressDialog = true
        },
        // 
        async lookLogistics() {
            this.logisticsDialog = true
            const { data: result } = await this.$axios.get('/kuaidi/1106975712662')

            console.log(result);
        },
    },
    mounted() {
        this.getOrderListData()
    },
    components: {

    },
};
</script>

<style scoped lang="less">
.el-cascader {
    width: 100%;
}
</style>
